body {
  background-image: url('https://static.wixstatic.com/media/261ef1_2b950a1f4c324d37aae7374098e7cdb7~mv2.gif');
  background-repeat: no-repeat;
  background-size: cover;
}

ul {
  list-style: none;
  padding: 0;
  font-size: 25px;
}

img {
  display: block;
}

.card-body {
  display: flex;
  justify-content: center;
}

.card-title {
  text-align: center;
  font-size: 50px;
}

.card-text {
  font-size: 20px;
}

.list-item {
  font-size: 15px;
  margin-bottom: 5px;
}

.search {
  display: block;
  margin: 150px auto 0;
}

.card-info {
  margin-right: 40px;
}

.countries-list {
  text-align: center;
}
